  text-align: center;
  margin: 2% 0;
  width: 100%;
}

.section_title p {
  color: #404040;
  font-size: 30px;
  display: inline-block;
  margin-bottom: 5px;
  text-align: center;
  text-transform: uppercase;
  padding: 0 12px;
  letter-spacing: 3px;
}

.section_title h3 {
  position: relative;
  font-size: 36px;
  text-transform: capitalize;
  line-height: 1.14;
  letter-spacing: 1.4px;
  padding-bottom: 13px;
  transition: 0.5s ease;
}

.section_title h3:after {
  content: "";
  position: absolute;
  display: block;
  height: 30px;
  width: 2px;
  bottom: -30px;
  left: 50%;
  background-color: #29b464;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  transition: 0.5s ease;
}

.section_title h3:hover {
  color: purple;
}

